html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  overflow-x: hidden;
  margin: 0;
  color: #282828;
  font-family: 'Montserrat', sans-serif; }

h1, h2, h3 {
  font-family: 'Inconsolata', monospace, 'Tahoma', sans-serif;
  font-weight: 400; }

a {
  color: #71a7b3;
  text-decoration: none;
  -webkit-transition: color 0.25s;
  transition: color 0.25s; }
  a svg.icon {
    height: 28px;
    width: 28px; }
    a svg.icon .background {
      -webkit-transition: fill 0.25s;
      transition: fill 0.25s;
      fill: #71a7b3; }

a:hover {
  color: #aad060; }
  a:hover svg.icon .background {
    fill: #aad060; }

.HomeComponent {
  margin: 80px; }
  .HomeComponent .top {
    display: flex;
    align-items: center; }
    .HomeComponent .top .NameCardComponent {
      margin-left: 40px; }
  .HomeComponent .columns {
    display: flex; }
    .HomeComponent .columns .leftColumn {
      margin-top: 40px;
      width: 250px; }
    .HomeComponent .columns .rightColumn {
      margin-left: 40px;
      margin-top: 40px; }

